@import '../../AppStyles.css';

.Main {
    width: 100%;
}

header {
    background-image: url(../../assets/images/bgImages/bg2.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 90vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #e1e1e1;
    
    h2 {
        font-size: clamp(20px, 5vw, 50px);
        text-shadow: 2px 2px 5px rgba(0, 0, 0, 5);
    }
    
    p {
        margin-top: 30px;
        font-size: clamp(12px, 2vw, 20px);
        max-width: 80%;
        text-shadow: 2px 2px 5px rgba(0, 0, 0, 5);
    }
    
    .app_mainButton.header {
        margin-top: 100px;
        padding: 15px 25px;
        box-shadow: 2px 2px 5px rgba(2, 2, 1, 1);
    }
}

.main_companyInfo_container {
    padding: 0px 15%;
    height: 400px;
    display: flex;
    width: 100%;
    margin-bottom: 30px;
    
    .main_companyInfo_text {
        width: 50%;
        text-align: center;
        align-content: center;
        
        h2 {
            font-size: clamp(20px, 4vw, 40px);
            line-height: 80px;
        }
        
        p {
            font-size: clamp(12px, 2vw, 20px);
        }
    }
    
    .main_companyInfo_imgContainer {
        width: 50%;
        text-align: center;
        align-content: center;
    }
}

.main_photogallery_container {
    padding: 0 15%;
}

@media (max-width: 480px) {
    .app_mainButton.header {
        padding: 5px 7px;
    }
}